import { Component, Element, Event, EventEmitter, h, Host, Method, Prop } from '@stencil/core';
import { parseClassNames } from '@utils/utils';
import { Modal } from 'bootstrap/dist/js/bootstrap';
import type { ToolbarItemInterface } from '../storm-toolbar/interface';

@Component({
  tag: 'storm-dialog',
  styleUrl: 'storm-dialog.css',
  shadow: false,
})
export class StormDialog {
  private _modal: Modal | undefined;
  private _modalRef!: HTMLElement;

  @Element() host!: HTMLElement;

  @Prop() buttons: ToolbarItemInterface[];
  @Prop() modalBackdrop: boolean = true;
  @Prop() modalFocus: boolean = true;
  @Prop() modalCentered: boolean = true;
  @Prop() modalScrollable: boolean = true;
  @Prop() heading: string = '';
  @Prop() size: '' | 'sm' | 'lg' | 'xl' = '';

  @Prop() bodyClasses: Record<string, boolean> | string = '';
  @Prop() headingClasses: Record<string, boolean> | string = 'h6';

  @Event() toolbarAction: EventEmitter<string>;

  @Method()
  async show() {
    this.getModal().show();
  }

  @Method()
  async hide() {
    this.getModal().hide();
  }

  getModal(): Modal {
    if (this._modal === undefined) {
      this._modal = new Modal(this._modalRef, {
        backdrop: this.modalBackdrop,
        focus: this.modalFocus,
      });
    }
    return this._modal;
  }

  headerRender() {
    if (!this.heading?.trim()) {
      return null;
    }

    return (
      <div class="modal-header">
        <div
          class={{
            'modal-title': true,
            'font-weight-normal': true,
            ...parseClassNames(this.headingClasses),
          }}
        >
          {this.heading}
        </div>
        <button type="button" class="btn-close text-dark" data-bs-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
    );
  }

  footerRender() {
    if (!this.buttons || this.buttons.length === 0) {
      return;
    }

    return (
      <div class="modal-footer">
        <storm-toolbar
          dataSet={this.buttons}
          onToolbarAction={e => {
            e.stopPropagation();
            this.toolbarAction.emit(e.detail);
          }}
        ></storm-toolbar>
      </div>
    );
  }

  private destroyModal() {
    if (this._modal) {
      try {
        this._modal.dispose();
      } catch (error) {
        console.error('Error disposing modal:', error);
      }
      this._modal = undefined;
    }
  }

  disconnectedCallback() {
    this.destroyModal();
  }

  render() {
    return (
      <Host>
        <div ref={elm => (this._modalRef = elm)} class="modal fade" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
          <div
            class={{
              'modal-dialog': true,
              'modal-dialog-centered': this.modalCentered,
              'modal-dialog-scrollable': this.modalScrollable,
              'modal-sm': this.size === 'sm',
              'modal-lg': this.size === 'lg',
              'modal-xl': this.size === 'xl',
            }}
            role="document"
          >
            <div class="modal-content">
              {this.headerRender()}
              <div class={{ 'modal-body': true, ...parseClassNames(this.bodyClasses) }}>
                <slot></slot>
              </div>
              {this.footerRender()}
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
